.card-car{

    border-radius: 10px;
    overflow: hidden;
    padding: 3px;
    
    margin-top: 10px;
    display: flex;
    border-radius: 5px;
    position: relative;
}

.card-car .right{
    flex-grow: 1;
    margin-left: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    position: relative;
}
.card-car .right p{
    margin-top: 5px;
    font-size: 12px;
}
.card-car .right .item{
    text-align: right;
    padding-right: 10px;
}
.card-car .left img{
    border-radius: 10px;
    width: 100px;
    height: 100%;
    display: block;
    object-fit: cover;
}
.card-car button{
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 3px;
    border: none;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 100%;
}
.buy-line{
    margin-top: 10px;
}