.loading {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 5px solid white;
    border-top: 5px solid #F9B524;
    animation: spin 1s linear infinite;
    position: absolute;
    left: 50%;
    
    
    
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  