.chat{
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    align-items: center;
    margin: 10px;
  
    padding: 5px;
    cursor: pointer;
    border-radius: 10px;
}
.chat div{
    margin-left: 10px;
}
.chat h1{
    width: 40px;
    height: 40px;
    background-color: white;
    text-align: center;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    border-radius: 100%;
}
.chat p{
    font-size: 13px;
}
.chat small{
    font-size: 10px;
}

.chat .point{
    background-color: red;
    width: 5px;
    height: 5px;
}