  padding: 10px;
}

.window-chat .info-chat img {
  width: 20px;
  height: 20px;
  cursor: pointer;
  transform: rotate(0deg);
  transition: all 300ms;
  border-radius: 100px;
}

.window-chat .chat-message {

  flex-grow: 1;
  display: flex;
  overflow-y: scroll;
  flex-direction: column;
}

::-webkit-scrollbar {
