.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 34px;
  transition: .4s;
}

.ch {
  display: none;
}

.between {
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 15px;
  margin-top: 20px;

}

.switch .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  border-radius: 50%;
  transition: .4s;
}

input:checked+.slider {
  background-color: #F9B524;
}

input:checked+.slider:before {
  transform: translateX(26px);
}

hr {
  background-color: red;
  display: block;


}

