@media (max-width:1200px) {
  .img-logo{
    display: block;
  }

  .container-modal .modal {
    width: 800px;
 

  
  }
  .container-modal .column {
    flex-basis: 900px;
 
  
  
  
  }
  .banner{
    position: absolute;
    left: 0;
  
    z-index: 999999;
    top: 0;
    margin-top: 70px;
    border: 1px solid #151414;
   transform: translateX(-360px);
    overflow: hidden;
  }
  .flex-box {
    grid-template-columns: 100%;
  }
  .header .icon-company{
    display: none;
  }
  .header .menu{
    display: flex;
  }
  #aside-bar:checked + .banner{
    transform: translateX(0px);
  }
  .main-content{
    left: -10px;
    right: -10px;
    position: fixed;
  }
  .header .car{
    display: none;
  }
  .header .menu-mobile{
    display: block;
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 24px;
  }
  
  .switch .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    border-radius: 34px;
    transition: .4s;
  }
  
  .ch {
    display: none;
  }
  

  
  .switch .slider:before {
    position: absolute;
    content: "";
    height: 10px;
    width: 10px;
    left: 4px;
    top: 30%;
    background-color: white;
    border-radius: 50%;
    transition: .4s;
  }
  
  input:checked+.slider {
    background-color: #F9B524;
  }
  
  input:checked+.slider:before {
    transform: translateX(20px);
  }
  .main-content{
    margin-left: 0px;
    width: 100%;
    
  }
  aside{
    display: none;
  }
}
